html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body, #root {
  height: 100vh;
  margin: 0;
}

body, input, select, button, textarea {
  font-size: 1.6rem;
  font-family: Arial, Helvetica, sans-serif;
}

.grid-container {
  display: grid;
  grid-template-areas: "header"
  "main"
  "footer";
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}

a{
  text-decoration: none;
  color: #353535;
}

a:hover {
  color: #1b85b6
}

header{
  grid-area: header;
  background-color: #353535;
  color: #fff;
  display: flex;
  align-items: center;
  padding: .5rem;
}

header a {
  color: #fff;
  text-decoration: none;
}

main{
  grid-area: main;
}

footer {
  grid-area: footer;
  background-color: #353535;
  color: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content{
  display: flex;
  flex-wrap: wrap;
}

.main {
  flex: 3 60rem;
}

.sidebar {
  flex: 1 20rem;
}


.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.products li{
  flex: 0 1 29rem;
  height: 47rem;
  padding: 1rem;
}

.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  background-color: #aaaaaa;
  border-radius: 0.4rem;
  border: #353535 0.1rem solid;
}

.product img {
  max-width: 37rem;
  max-height: 37rem;
}

.product p {
  display: flex;
  justify-content: center;
  font-weight: 800;
}

.product-price{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-price div, .product-price button {
  text-align: center;
  flex: 1;
}

.product-price div {
  font-size: 2rem;
}

.button{
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
  background-color: #f0f0f0;
}
.button:hover{
  border: .1rem #707070 solid;
}

.button.primary{
  background-color: #468eaf;
}

.filter {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: 0.1rem #ccc solid;
}

.filter div {
  flex: 1;
}




.cart{
  padding: 1rem;
  margin: 1rem;
  display: flex;
}


.cart-header {
  border-bottom: 0.1rem #c0c0c0 solid;
}

.cart-items {
  padding:0;
  width: 100%;
  list-style-type: none;
}

.cart-items img {
  width: 5rem;
}

.cart-items li {
  display: flex;
  background-color: #e7e7e7;
  margin: 0.5rem;
}

.cart-items li div {
  padding: .5rem;
}

.cart-items li div:last-child {
  flex: 1;
}

.right{
  text-align: right;
}

.total {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  background-color: #3f8a5c;
  color: #f1f1f1;
  font-weight: 600;
  border-radius: 0.8rem;
  font-size: 1.5rem;
  padding: 0.5rem;
}

.total >div {
  flex: 1;
}